

















{ Query, read_at } = require "~plugins/memory-record"

module.exports =
  default:
    data: ->
      menus: []

    methods:
      back: ->
        { ids } = @$route.query
        chat = Query.chats.find(ids[0])
        console.log chat.book_id, chat.part_id, chat.id
      
      anker: (ids)->
        set = {}
        for id in ids
          set[id] = true
        for id in @$route.query.ids
          set[id] = true
        ids = Object.keys set
        @$router.push
          path: "/ankers"
          query: { ids }

    computed:
      chat_pages: ->
        { ids } = @$route.query
        Query.chats.ankers(ids)




